
.wrapper{
    margin: 0 auto;
}
@media (max-width: 3500px) {
    .wrapper{
        overflow: hidden;
    }
}
@media (min-width: 1201px) {
    .wrapper{width: 1135px;}
}
@media (max-width: 1200px) {
    .wrapper{max-width: 96%;}
}
@media (max-width: 992px) {
    .wrapper{
        max-width: 96%;}
}
@media (max-width: 768px) {
    .wrapper{
        margin: 0 auto;
        max-width: 96%;
    }
}
@media (max-width: 576px) {
    .container{
        max-width: 96%;
        padding: 0 10px;
        box-sizing: border-box;
    }
}