.author{
    margin: 40px auto;
    height: auto;
    padding: 8px;
    border-radius: 20px;
    text-indent: 10px;
    box-shadow: 0px 5px 8px 4px rgba(20, 127, 227, 0.2);

    &__photo{
        border-radius: 50%;
        object-fit: contain;
        width: 100px;
        height: 100px;
        float: left;
        box-shadow: -16px -15px 8px 7px #fbfbfb;
    }
 
}
@media (max-width: 3500px) {
    .author{
        width: 78%;
        padding-bottom: 30px;
    }
}
@media (max-width: 692px) {
    .author{
        width: 300px;
        padding-bottom: 10px;
    }
}